@use '../abstracts' as a;

.projects {
    padding-top: 100px;

    &__container {
        margin-top: 30px;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        gap: 50px;

        &__card {
            box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
            
            &__image {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 200px;
                overflow: hidden;

                img {
                    filter: brightness(0.98);
                    transform: scale(1.3);
                }
            }
        }
    }
}